.projectsWrapper {
    height: 100%;
}

.containerInfo {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
}

.projects {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.blockTitle {
    margin: 0 auto;
    width: 100%;
    position: relative;
    padding: 50px 0;
    text-align: center;
}

.blockTitle h1 {
    font-size: calc(32px + (56 - 32) * ((100vw - 320px) / (1440 - 320)));
    font-weight: 900;
    color: #fff;
    text-transform: uppercase;
    margin: 0;
}

.blockTitle h1 span{
    color: #ffb400;
    padding-left: 5px;
}

.titleBg {
    font-size: calc(60px + (110 - 60) * ((100vw - 320px) / (1440 - 320)));
    left: 0;
    letter-spacing: 10px;
    line-height: .7;
    position: absolute;
    right: 0;
    top: 50%;
    text-transform: uppercase;
    font-weight: 800;
    transform: translateY(-50%);
    color: hsla(0,0%,100%,.07);
}