.contactsWrapper {
    height: 100%;
    padding-bottom: 7%;
}

.containerInfo {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.blockTitle {
    margin: 0 auto;
    width: 100%;
    position: relative;
    padding: 50px 0;
    text-align: center;
}

.blockTitle h1 {
    font-size: calc(32px + (56 - 32) * ((100vw - 320px) / (1440 - 320)));
    font-weight: 900;
    color: #fff;
    text-transform: uppercase;
    margin: 0;
    padding-top: 10px;
}

.blockTitle h1 span{
    color: #ffb400;
    padding-left: 5px;
}

.titleBg {
    font-size: calc(46px + (110 - 46) * ((100vw - 320px) / (1440 - 320)));
    left: 0;
    letter-spacing: 10px;
    line-height: .7;
    position: absolute;
    right: 0;
    top: 50%;
    text-transform: uppercase;
    font-weight: 800;
    transform: translateY(-50%);
    color: hsla(0,0%,100%,.07);
}

.contactsBlock {
    display: flex;
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
    margin: 0 10px;
}

.contactsInfo {
    width: 300px;
}

.contactsTitle {
    font-size: 26px;
    font-weight: 600;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    color: #fff;
}

.contactsTitle span {
    color: #ffb400;
    padding-left: 5px;
}

.contactsText {
    font-family: "Open Sans",sans-serif;
    color: #fff;
}

.addressInfo {
    font-weight: 600;
    line-height: 25px;
    margin-left: 60px;
    padding-top: 5px;
    margin-bottom: 20px;
    font-family: "Open Sans",sans-serif;
    color: #fff;
}

.addressInfo span {
    text-transform: uppercase;
    opacity: .8;
    font-weight: 400;
    padding-bottom: 0;
    display: block;
}

.socialNetworkList {
    padding-inline-start: 0;
    display: flex;
    justify-content: center;
}

.socialNetworkList li {
    display: inline-block;
}

.socialNetworkList li .telegram {
    display: inline-block;
    height: 40px;
    width: 40px;
    line-height: 42px;
    text-align: center;
    color: #fff;
    transition: .3s;
    font-size: 17px;
    margin: 0 6px;
    background: #2b2a2a;
    border-radius: 50%;
    text-decoration: underline;
    background-image: url("../../assets/images/telegram.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
}

