.formWrapper {
    width: calc(300px + (650 - 300) * ((100vw - 320px) / (1440 - 320)));
    margin: 0 10%;
}

.formBlock {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.yourNameBlock {
    width: calc(280px + (650 - 280) * ((100vw - 320px) / (1440 - 320)));
}

.formGroup {
    margin-bottom: 30px;
}

.inputName {
    border: 1px solid #111;
    width: 100%;
    background: #252525;
    color: #fff;
    padding: 10px;
    border-radius: 30px;
    outline: none;
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}

.yourMailBlock {
    width: calc(280px + (650 - 280) * ((100vw - 320px) / (1440 - 320)));
}

.yourSubjectBlock {
    width: 100%;
}

.inputSubject {
    border: 1px solid #111;
    width: calc(280px + (650 - 280) * ((100vw - 320px) / (1440 - 320)));
    background: #252525;
    color: #fff;
    padding: 10px;
    border-radius: 30px;
    outline: none;
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}

.yourMessageBlock {
    width: calc(280px + (650 - 280) * ((100vw - 320px) / (1440 - 320)));
}

.textareaMessage {
    border: 1px solid #111;
    background: #252525;
    color: #fff;
    width: calc(280px + (650 - 280) * ((100vw - 320px) / (1440 - 320)));
    padding: 10px;
    height: 200px;
    overflow: hidden;
    border-radius: 30px;
    outline: none;
    resize: vertical;
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}

.btnWrapper {
    margin: 0 auto;
}

.btnMain {
    border: 2px solid #ffb400;
    font-size: 15px;
    font-weight: 700;
    color: #fff;
    font-family: inherit;
    text-decoration: none;
    box-sizing: border-box;
    text-transform: uppercase;
    background-color: rgba(255, 255, 255, 0);
    display: inline-block;
    width: 250px;
    text-align: center;
    line-height: 50px;
    overflow: hidden;
    cursor: pointer;
    transition: all .25s ease-in-out;
    position: relative;
    z-index: 1;
}

.btnMain::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background-color: #ffb400;
    top: 0;
    right: -100%;
    transition: .3s;
    z-index: -1;
}

.btnMain:hover::before {
    right: 0;
}

