.navWrapper {
    width: calc(320px + (500 - 320) * ((100vw - 320px) / (1440 - 320)));
    display: flex;
    justify-content: space-evenly;
    padding: 0 2%;
}

.navLink {
    font-weight: 700;
    text-decoration: none;
    color: #fff;
    font-family: 'Poppins',sans-serif;
    font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1440 - 320)));
    padding: 0 2%;
}

.navLink:hover {
    color: #ffc107;
}
