.footerWrapper {
    background-color: rgba(21, 21, 21, 0.8);
    width: 100%;
}

.containerInfo {

}

.mySocialNetwork {
    width: 100%;
    display: flex;
    justify-content: center;
}

.footerTitle h3{
    font-size: calc(15px + (20 - 15) * ((100vw - 320px) / (1440 - 320)));
    font-weight: 600;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    color: #fff;
    margin-block-start: 0;
    margin-block-end: 0;
    display: flex;
    justify-content: center;
    padding-top: calc(10px + (15 - 10) * ((100vw - 320px) / (1440 - 320)));
}


.socialNetworkList {
    padding-inline-start: 0px;
    display: flex;
    margin-block-start: 7px;
    margin-block-end: 7px;
}

.socialNetworkList li {
    display: inline-block;
}

.socialNetworkList li .github {
    display: inline-block;
    height: calc(25px + (30 - 25) * ((100vw - 320px) / (1440 - 320)));
    width: calc(25px + (30 - 25) * ((100vw - 320px) / (1440 - 320)));
    line-height: 42px;
    text-align: center;
    color: #fff;
    transition: .3s;
    font-size: 17px;
    margin: 0 10px;
    background: #2b2a2a;
    border-radius: 50%;
    text-decoration: underline;
    background-image: url("../../assets/images/github.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
}

