.mainWrapper {
    height: 100%;
}

.containerInfo {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding-top: calc(80px + (120 - 80) * ((100vw - 320px) / (1366 - 320)));
}

.photo {
    width: calc(280px + (350 - 280) * ((100vw - 320px) / (1366 - 320)));
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.9);
    border-radius: 30px;
    background-image: url("../../assets/images/photo.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    height:  calc(350px + (500 - 350) * ((100vw - 320px) / (1366 - 320)));
}

.text {
    width:  calc(300px + (700 - 300) * ((100vw - 320px) / (1440 - 320)));
    color: #ffffff;
}

.textTitle {
    font-family: 'Poppins', sans-serif;
    color: #ffc107;
    font-size: calc(30px + (45 - 30) * ((100vw - 320px) / (1440 - 320)));
    font-weight: 700;
}

.textTitle span {
    color: #fff;
    display: block;
}

.textPresent {
    width: calc(300px + (600 - 300) * ((100vw - 320px) / (1440 - 320)));
    font-family: "Open Sans", sans-serif;
    margin: 15px 0 28px;
    font-size: 18px;
    line-height: 35px;
}

.btnWrapper {

}

.btnMain {
    border: 2px solid #ffb400;
    font-size: 15px;
    font-weight: 700;
    color: #fff;
    font-family: inherit;
    text-decoration: none;
    box-sizing: border-box;
    text-transform: uppercase;
    display: inline-block;
    width: 250px;
    text-align: center;
    line-height: 50px;
    overflow: hidden;
    cursor: pointer;
    transition: all .25s ease-in-out;
    position: relative;
    z-index: 1;
}

.btnMain::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background-color: #ffb400;
    top: 0;
    right: -100%;
    transition: .3s;
    z-index: -1;
}

.btnMain:hover::before {
    right: 0;
}

@media screen and(max-width: 644px) {
    .photo {
        width: 250px;
        height: 250px;
        border-radius: 50%;
    }

    .text {
        width: 100%;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }

    .textTitle {
        font-size: 25px;
    }

    .textPresent {
        width: auto;
    }
}

@media screen and(max-width: 320px) {
    .photo {
        padding: 20px;
        width: 150px;
        height: 150px;
        border-radius: 50%;
    }
    .textPresent {
        width: 100%;
    }


}



